<template>
    <v-layout
        column
        align-center
        justify-center
    >
        <v-card
        class="ma-3"
        width="80vw"
        style="border-radius: 10px;"
        height="60vh"
        hover
        v-for="(field, index) in selectedFields"
        :key="index"
    >

            <v-toolbar color="primary"
                class="mb-2"
            >
                <v-toolbar-title class="white--text">
                    {{ field }} Chart
                </v-toolbar-title>

                <v-spacer></v-spacer>

                <v-btn color='secondary'>
                    <v-icon class="mr-2">
                        fas fa-file-export
                    </v-icon>
                    Export As PNG
                </v-btn>
            </v-toolbar>

            <GChart
                type="LineChart"
                :data="getChartData(field)"
                :options="chartOptions(field)"
                class="chart"
            ></GChart>
        </v-card>
    </v-layout>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Diagnostics',
  data: () => ({
    diagFields: [
      'RSSI',
      'SNR',
      'batteryV',
      'hAcc',
      'msss',
      'numSV',
      'pdop',
      'ttff',
    ],
  }),
  computed: {
    ...mapState({
      series: 'tsData',
      selected: 'selected',
    }),
    selectedFields() {
      const attributes = Object.getOwnPropertyNames(this.selected.data);
      console.log(attributes);
      const selectedFields = [];
      for (let i = 0; i < attributes.length; i += 1) {
        if (this.diagFields.includes(attributes[i])) {
          selectedFields.push(attributes[i]);
        }
      }
      return selectedFields;
    },
  },
  methods: {
    getChartData(field) {
      const chartData = [];
      chartData.push(['Time', `${field}`]);
      const dataLen = this.series.length;
      console.log(this.series);
      for (let i = 0; i < dataLen; i += 1) {
        chartData.push([this.series[i].time, this.series[i].data[field]]);
      }
      return chartData;
    },
    chartOptions(field) {
      return {
        title: `${field} over 24 Hours`,
        hAxis: {
          title: 'Time',
          titleTextStyle: {
            color: '#004346',
          },
        },
        vAxis: {
          title: `${field}`,
          titleTextStyle: {
            color: '#004346',
          },
        },
        colors: ['#74B3CE'],
      };
    },
  },
  mounted() {
    this.getChartData();
    this.chartOptions();
  },
};
</script>

<style scoped>
  .chart {
    width: 100%;
    height: 80%;
  }
</style>
